.trp-language-wrap {
	display: none;
}

.translate_menu {
	position: fixed;
	box-sizing: border-box;
	top: 100vh;
	right: 3rem;
	height: 12rem;
	z-index: 50000;
	padding: 0;
	border-radius: 0.125rem;

	a {
		display: block;
		color: color('grey-mid');
		font-size: 0.925rem;
		&:hover {
			color: color('grey-light');
		}
	}

	.layout_wrapper {
		position: relative;
		top: 0;
		transition-property: top;
		transition-duration: 250ms;
		transition-timing-function: ease-out;;
	}

	.open-lang-menu {
		box-sizing: border-box;
		display: block;
		position: absolute;
		top: -2.5rem;
		background-color: color('black');
		border-top-right-radius: 3px;
		border-top-left-radius: 3px;
		width: 3.5rem;
		height: 2.5rem;
		padding: 1rem;
	}

	.translation-list {
		box-sizing: border-box;
		background-color: color('black');
		margin: 0;
		padding: 0 0 3rem 0;
		border-top-right-radius: 3px;
		list-style: none;

		li {
			white-space: nowrap;
			list-style: none;
			padding: 0;
			margin: 0;

			a {
				color: #999;
				display: block;
				padding: 0.5rem 2rem 0.5rem;
				text-decoration: none;
				&:hover {
					background-color: #333;
					color: #dedede;
				}
			}
			&.current {
				order: -1;
				a {
					color: #fff;
					font-weight: 700;
				}
			}
		}
	}

	svg {
		width: 1.5rem;
		path {
			fill: #fff;
		}
	}

	&.open {
		.layout_wrapper {
			top: -7rem;
		}
	}
}
