.header-notice {
	background-color: color('green-dark');
	height: 3rem;
	h2 {
		height: 100%;
		vertical-align: middle;
		letter-spacing: 1px;
		text-align: center;
		margin: 0;
		font-size: 18px;
		text-transform: unset;
		padding-top: 0.275rem;
		line-height: 2;
	}

	a {
		text-decoration: none;
		display: block;
		height: 100%;
		color: #fff;
		vertical-align: middle;
		&:focus,
		&:hover {
			text-decoration: underline;
		}
	}
}
